.Button {
    background-color: #4CAF50;
    /* Green */
    border: none;
    color: white;
    padding: 16px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    transition-duration: 0.4s;
    cursor: pointer;
}


.ButtonEffect {
    background-color: white;
    color: black;
    border: 2px solid #008CBA;
}

.ButtonEffect:hover {
    background-color: #008CBA;
    color: white;
}